:host {
  display: block;
}

:host.has-widgets .home-placeholder {
  display: none !important;
}

.home-header {
  margin: 0 0 12px;
}

.home-header h1 {
  margin: 0;
  font-size: 22px;
  font-weight: 600;
  color: #0f172a;
}

.home-placeholder {
  margin: 16px auto 20px;
  padding: 28px 32px;
  border: 1px solid rgba(148, 163, 184, 0.25);
  border-radius: 12px;
  background: linear-gradient(180deg, #f8fafc, #ffffff);
  box-shadow: 0 16px 40px rgba(15, 23, 42, 0.08);
  display: grid;
  place-items: center;
  text-align: center;
  gap: 16px;
  width: 100%;
  max-width: 760px;
}

.home-placeholder .illustration {
  width: 200px;
  height: 128px;
  border-radius: 12px;
  background:
    linear-gradient(135deg, rgba(96, 165, 250, 0.18) 25%, transparent 25%) -12px 0 / 24px 24px,
    linear-gradient(225deg, rgba(96, 165, 250, 0.18) 25%, transparent 25%) -12px 0 / 24px 24px,
    linear-gradient(315deg, rgba(59, 130, 246, 0.18) 25%, transparent 25%) 0 0 / 24px 24px,
    linear-gradient(45deg, rgba(14, 165, 233, 0.18) 25%, transparent 25%) 0 0 / 24px 24px,
    linear-gradient(180deg, rgba(59, 130, 246, 0.08), rgba(37, 99, 235, 0.18));
  box-shadow:
    inset 0 1px 0 rgba(255, 255, 255, 0.8),
    0 12px 28px rgba(37, 99, 235, 0.16);
}

.home-placeholder h2 {
  margin: 0;
  font-size: 16px;
  font-weight: 600;
  color: #0f172a;
  max-width: 460px;
}

.home-placeholder .muted {
  margin: 0;
  font-size: 13px;
  color: #64748b;
  max-width: 440px;
}

.home-placeholder .primary {
  border: none;
  background: linear-gradient(135deg, rgba(2, 62, 138, 0.9), rgba(0, 119, 182, 0.9));
  color: #fff;
  border-radius: 999px;
  padding: 0 20px;
  height: 32px;
  cursor: pointer;
  font-size: 13px;
  font-weight: 600;
  box-shadow: 0 6px 18px rgba(2, 62, 138, 0.2);
  transition: background 0.18s ease, box-shadow 0.18s ease;
}

.home-placeholder .primary:hover {
  background: linear-gradient(135deg, rgba(2, 62, 138, 0.96), rgba(0, 119, 182, 0.96));
  box-shadow: 0 8px 20px rgba(2, 62, 138, 0.24);
}

.home-placeholder .primary:active {
  background: linear-gradient(135deg, rgba(2, 62, 138, 1), rgba(0, 119, 182, 1));
  box-shadow: 0 5px 14px rgba(2, 62, 138, 0.22);
}

.gs-container {
  width: 100%;
  min-height: 0;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.grid-stack {
  background: transparent;
  width: 100%;
  box-sizing: border-box;
  min-height: 0;
}

.block {
  width: 100%;
  height: 100%;
  background: #fff;
  border: 1px solid var(--ring);
  border-radius: 12px;
  box-shadow: 0 2px 6px rgba(15, 23, 42, 0.08);
  display: grid;
  grid-template-rows: 36px 1fr;
  overflow: hidden;
}

.block .hd {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 10px;
  font-size: 12px;
  color: #111827;
  border-bottom: 1px solid #f1f5f9;
  background: #fafafa;
}

.block .hd .actions {
  display: flex;
  gap: 6px;
}

.block .hd button {
  border: 1px solid #e5e7eb;
  background: #fff;
  height: 22px;
  padding: 0 8px;
  border-radius: 6px;
  cursor: pointer;
}

.block .bd {
  padding: 12px 14px 16px;
  color: #4b5563;
  font-size: 12px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  height: 100%;
  box-sizing: border-box;
  overflow: hidden;
}

.grid-stack-placeholder > .placeholder-content {
  border-radius: 12px;
  border: 2px dashed rgba(99, 102, 241, 0.45);
  background: rgba(99, 102, 241, 0.08);
  transition: all 0.12s ease;
}

.gs-drag-helper {
  position: absolute;
  z-index: 2000;
  pointer-events: none;
  border-radius: 12px;
  box-shadow: 0 20px 50px rgba(15, 23, 42, 0.24);
  opacity: 0.96;
  transform: scale(1.01);
  transition: transform 0.12s ease, box-shadow 0.12s ease, opacity 0.12s ease;
}

.gs-drag-helper .block {
  box-shadow: none;
}

.gs-drag-helper--active {
  transform: scale(1.03);
  box-shadow: 0 24px 60px rgba(15, 23, 42, 0.28);
}
